.hide {
    display: none;
}
#Header {
    display: flex;
    padding: 0px 20px;
    justify-content: space-between;
    align-items: baseline;
    z-index: 2;
}

#LogoA {
    text-decoration: none;
}

#Logo {
    color: white;
    font-size: 33px;
    font-family: 'Titillium';
    font-weight: 300;
    letter-spacing: 3px;
}

#TrigramFire {
    font-size: 45px !important;
    user-select: none;
}

#HeaderNavMenu {
    width: max-content;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.NavItem {
    text-decoration: none;
    color: white;
    font-size: 22px;
    font-family: 'Titillium';
    font-weight: 200;
    letter-spacing: 3px;
    padding: 10px 15px;
    margin: 0;
    user-select: none;
    cursor: pointer;
    animation: 2s;
    border-bottom: 2px solid transparent;
    transition: .15s;
}

.NavItem:hover {
    border-bottom: 2px solid white;
}