#MainHero {
    height: 90vh;
    display: flex;
    align-items: center;
    padding-bottom: 10px;
}

#MainHeroText {
    padding: 0 20px;
    max-width: 42vw;
    position: absolute;
    z-index: 2;
}

#MainHeroHeading::selection, #MainHeroSubText::selection {
    background-color: black;
    color: white;
}

#MainHeroHeading {
    color: white;
    font-family: 'Titillium';
    font-size: 35px;
}

#MainHeroSubText {
    color: #EEE;
    font-size: 25px;
    font-weight: 400;
    letter-spacing: 2px;
}

#MainHeroImage {
    width: 100vw;
    pointer-events: none;
    user-select: none;
    border-radius: 1px;
    mask-image: linear-gradient(to top, rgba(0,0,0,1), rgba(0,0,0,.75), rgba(0,0,0,0));
    filter: brightness(1.2);
    filter: contrast(1.1);

}