#TechStackHero {
    height: min-content;
    padding: 80px 0px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

#TechStackHeroText {
    width: 45vw;
    margin-right: 30px;
   
}

#TechStackHeroHeading {
    color: white;
    font-family: 'Titillium';
    font-size: 35px;
}

#TechStackHeroSubText {
    color: white;
    font-family: 'Titillium';
    font-size: 20px;
}

#TechStackHeroHeading::selection, #TechStackHeroSubText::selection {
    background-color: white;
    color: black;
}


#TechStackHeroImage {
    width: 50vw;
}